get":"_blank"}},[_vm._v("Twitter")]),_vm._v(". These updates provide localized information, which has the potential to enhance crisis awareness and response. However, with limited resources, crisis managers may endure information overload from the inundation of these updates. Prior work has demonstrated the potential of machine learning (ML) methodologies to mitigate this problem. We have identified limitations in the prior work including the lack of involvement of crisis managers in the development and evaluation of a ML methodology. ")]),_c('p',[_vm._v(" To address these limitations, we propose a novel framework and ML methodology which investigate the efficacy of various ML methods in enhancing crisis awareness and response beyond model performance metrics. This framework aims to iteratively embed the information needs and priorities of crisis managers during crisis into the design of the ML methodology. We cooperated with crisis managers in Fukuchiyama City (FC), a city in Japan which is susceptible to flood events, and analyzed crowdsourced crisis image and text data from past FC flood events. We devised the Flood Presence image classification task, constructed Train/Dev/Test splits, and annotated images from FC. We report a weighted F1 score of 92.1% on the test split and 82.5% on the FC images. Using the results of our image analysis ML methodology and the insights we gained from crisis managers, we iterated on the design of our text analysis ML methodology. This led to the creation of the Human Risk text classification task which is tailored to a subset of the identified information needs of the crisis managers. To align with the priorities of crisis managers for this task, we determined the model evaluation metric to be the F2 score. We report an F2 score of 92.8% on an FC crisis text test dataset, which is a significant improvement over the baseline score of 43.4%. ")]),_c('h5',[_c('strong',[_vm._v("Research Question")])]),_c('strong',{attrs:{"id":"research-question"}},[_vm._v(" In collaborating with crisis managers, how can machine learning methods be utilized and evaluated to effectively reduce the information overload of crowdsourced crisis data on crisis managers during flood crisis events for enhanced crisis awareness and response? ")])])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"carousel-text"},[_c('h5',[_vm._v("Main Research Aims")]),_c('p',{attrs:{"id":"research-aims"}},[_c('ol',[_c('li',[_c('strong',[_vm._v("To reduce information overload during a crisis")]),_vm._v(" using accurate, efficient, automated image and text classification of crisis reports by machine learning (ML) models during crisis.")]),_c('li',[_c('strong',[_vm._v("To embed tacit knowledge, information needs, and decision-making priorities of crisis managers")]),_vm._v(" into the designed ML methodology.")]),_c('li',[_c('strong',[_vm._v("To evaluate methodology in collaboration with crisis managers")]),_vm._v(", e.g. crisis managers in Fukuchiyama, Japan.")]),_c('li',[_c('strong',[_vm._v("To incorporate evaluation results and iterate on the ML methodology")]),_vm._v(" in order to better reach aim of using AI to assist crisis managers during crisis.")])]),_vm._v(" We investigate various ML techniques that can be applied to mitigate information overload for crisis managers during crisis events while also assessing if those techniques can satisfy the information needs and priorities of crisis managers through qualitative and quantitative evaluation. ")]),_c('h5',[_vm._v("Novel Framework")]),_c('p',[_vm._v(" To achieve the aims above we develop a novel framework in the crisis informatics community consisting of the following components: "),_c('ul',[_c('li',[_c('strong',[_vm._v("Classification Task Creation")]),_vm._v(" - We develop new classification tasks using labels provided to us by crisis managers and labels present in open-source datasets")]),_c('li',[_c('strong',[_vm._v("Data Annotation Procedure")]),