achine Learning Methodology")]),_vm._v(" With our crisis management partners in Fukuchiyama (FC), Japan, we present our framework through two ML modules: Image Analysis Module & Text Analysis Module ")])]),_c('div',{staticClass:"carousel-item cc-carousel-item"},[_c('div',{staticClass:"row align-items-center justify-content-around"},[_c('div',{staticClass:"col-6 pt-3 pb-5"},[_c('img',{staticClass:"img-fluid",attrs:{"id":"image-analysis-module","src":require("../../../public/assets/image-analysis-module.png"),"alt":"Second slide"}})]),_c('div',{staticClass:"carousel-text col-6"},[_c('h5',[_vm._v("Image Analysis Methodology Overview")]),_c('p',[_vm._v(" The goal of the Image Analysis Module is to utilize pretrained Convolutional Neural Network models to yield efficient and accurate predictions from image data in crowdsourced crisis reports. The classification tasks of Damage Severity (DS), Humanitarian Categories (HC), Informativeness (IN), and Flood Presence (FP) form a diverse suite of labels. In a fraction of a second, the model predictions made for these tasks provide a series of categorizations for an individual report. We leverage state-of-the-art CNNs, which strike a necessary balance between model complexity, memory and storage constraints, and model performance to provide these predictions. "),_c('br'),_c('br'),_vm._v(" To achieve this aim, we use large, labeled, open-source datasets for training and evaluating the models. We formulate a new crisis image classification task for detecting flood presence in an image and construct a new dataset altogether using flood images from various open-source datasets, which contained flood-adjacent labels. We additionally devise an annotation procedure and analyze the results of human-annotations on crisis images provided to us by crisis managers in Fukuchiyama. We held various image annotation workshops with crisis managers to identify the limitations in our approach and understand how we could iterate on the design of ou